/**
 * WordPress dependencies
 */
import { createReduxStore, register } from "@wordpress/data";

import { DEFAULT_STATE, SET_APPS, STORE_NAME, IS_LOADING } from "./constants";

import { getApps } from "./api";

// Define our actions
const actions = {
	setApps(apps) {
		return {
			type: SET_APPS,
			payload: { apps },
		};
	},

	setIsLoading(status) {
		return {
			type: IS_LOADING,
			payload: {
				status,
			},
		};
	},
};

// Define the reducer
function reducer(state = DEFAULT_STATE, { type, payload }) {
	switch (type) {
		case SET_APPS: {
			return {
				...state,
				...payload,
			};
		}
		case IS_LOADING: {
			const { status } = payload;
			return {
				...state,
				isLoading: status,
			};
		}
	}
	return state;
}

// Define some selectors
const selectors = {
	getApps(state) {
		return state.apps;
	},
	getIsLoading(state) {
		return state.isLoading;
	},
};

const resolvers = {
	getApps() {
		return async ({ dispatch }) => {
			dispatch.setIsLoading(true);
			const apps = await getApps();
			dispatch.setApps(apps);
			dispatch.setIsLoading(false);
		};
	},
};

// Define and register the store.
const store = createReduxStore(STORE_NAME, {
	reducer,
	actions,
	selectors,
	resolvers,
	__experimentalUseThunks: true, // Fallback for those not using WP 6.0
});

register(store);
